h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2;
  font-weight: bold;
  margin-bottom: 1rem;
  color: #000;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.5em;
}

h3 {
  font-size: 1.17em;
}

h4 {
  font-size: 1em;
}

h5 {
  font-size: .83em;
}

h6 {
  font-size: .67em;
}

a {
  color: #1e8bc3;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}

b {
  font-weight: bold;
}

p {
  line-height: 1.5;
}

body pre {
  font-family: 'Roboto Mono', monospace;
  background-color: #f5f6fa;
  color: #353b48;
  border-radius: $main-border-radius;
  padding: 1rem;
  overflow-x: auto;
  line-height: 1.2;
  &.prettyprint {
    border: 0;
    padding: 1rem;
    * {
      transition: color .5s ease-in-out;
    }
  }
}

input,
button,
textarea {
  font-size: inherit;
  border-radius: $main-border-radius;
  border: .1rem solid $color-gray;
  padding: .5rem;
  font-family: inherit;
  transition: box-shadow .2s;
}

button {
  color: #fff;
  background-color: $main-color;
  border-color: $main-color;
  font-weight: bold;
  &:disabled {
    background-color: #aaa;
    border-color: #aaa;
  }
  &.large {
    font-size: 1.2rem;
    padding: .8rem 1rem;
  }
}

input:focus,
button:focus,
textarea:focus {
  border-color: $main-color;
  box-shadow: 0 0 0 $main-border-width $main-color;
}
