* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  box-sizing: border-box;
  line-height: 1;
  &::before,
  &::after {
    box-sizing: inherit;
  }
}

html,
body {
  width: 100%;
  height: 100%;
}

body {
  font-family: 'Roboto', sans-serif;
  background-color: #fff;
  color: #323232;
  font-size: 16px;
  font-weight: normal;
  overflow-y: scroll;
}
